import { createSlice } from "@reduxjs/toolkit";

//userState 
const initialState = {
    isLoggedIn: false,
    name: 'guest'
}

const userSlice = createSlice({
/* 
Slice -> Redux/toolkit createSlice()  state+reducer   

 userSlice  
 - name
 - reducers
 - initailState
 - action
 - reducer
*/
    name: 'user',
    initialState,
    reducers: {
        login(state, action){
            state.isLoggedIn = true;
            state.name = action.payload.name;
        },
        logout(state){
            state.isLoggedIn = false;
            state.name = 'guest'   
        }
    }
})

export const {login, logout} = userSlice.actions;
export default userSlice.reducer;