import React from 'react';
import {login} from '../redux/userReducer'
import { useDispatch } from 'react-redux';

const LoginPage = () => {
    const dispatch = useDispatch();
    const btnLogin = ()=> {
        dispatch(login({name: ''}))
    }
  return (
    <div className='login'>
        <h2></h2>
        <button onClick={btnLogin}></button>

    </div>
  )
}

export default LoginPage